body{
  margin:0;
  padding: 0;
}
.App{
  text-align: center;
  margin: 50px auto 0px;
  width: 90%;
  max-width: 600px;
  border-radius: 20px;
  height: 420px;
  padding: 20px 15px;
  background-color: rgb(29, 221, 255);
  box-shadow: rgba(17, 12, 46, 0.15) 0px 48px 100px 0px;
}
.search-engine{
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-top: 10px;
  margin-bottom: 30px;
}
.search-engine input{
  width:70%;
  height: 45px;
  border: 1px solid;
  border-radius: 8px;
  padding: 2px 15px ;
  font-size: 16px;
  outline: none;
  background-color: #ffffff;
  color: #555555;
}
.search-engine input:hover {
  border: solid 1px black;
}
.search-engine button{
  border: none;
  background-color: black;
  color: #ffffff;
  border-radius: 8px;
  padding:13px 15px ;
  font-size: 20px;
  outline: none;
  cursor: pointer;
}
.search-engine button:hover {
  background-color: rgb(32, 32, 32);
}
.city-name{
  margin-bottom: 10px;
}
.loading{
  font-size: 60px;
  font-weight: bolder;
  color: #000;
}
.date{
  font-size: 18px;
  font-weight: 500;
  font-style: italic;
}
.temp{
  font-size: 64px;
  color: #000;
  font-weight: bold;
  text-align: center;
}
.description{
  color: #000000;
  font-size: 22px;
  font-weight: 500;
  margin-top: 0px;
  margin-bottom: 20px;
}
.weather-info{
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  margin-top: 20px;
  padding: 0px 20px;
  font-size: 20px;
  font-weight: bold;
  text-align: center;
}
.column{
  display: flex;
  align-items: center;
  line-height: 10px;
}
@media screen and (max-width:400px){
  .column{
    display: flex;
  align-items: center;
  line-height: 22px;

  
  }
}